"use client";

import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Form, FormField, FormItem, FormLabel, FormControl, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectTrigger, SelectContent, SelectItem, SelectValue } from "@/components/ui/select";
import { getRandomQuestion, getAvailablePositions } from "@/lib/interview";

type EvaluationFormData = {
  rating: string;
  feedback: string;
  candidateName: string;
};

export default function InterviewGeneratorPage() {
  const [selectedPosition, setSelectedPosition] = useState<string>("");
  const [question, setQuestion] = useState<string>("");
  const [error, setError] = useState<string>("");
  const [isSubmitted, setIsSubmitted] = useState<boolean>(false);
  
  const form = useForm<EvaluationFormData>({
    defaultValues: {
      rating: "",
      feedback: "",
      candidateName: "",
    },
  });

  const positions = getAvailablePositions();

  const generateQuestion = () => {
    if (!selectedPosition) {
      setError("Please select a position first.");
      return;
    }
    setError("");
    setIsSubmitted(false);
    try {
      const generated = getRandomQuestion(selectedPosition);
      setQuestion(generated);
    } catch (err) {
      setError("Error generating question. Please try again.");
    }
  };

  const onSubmit = (data: EvaluationFormData) => {
    // In a real-world scenario, you would send this data to an API
    console.log("Evaluation Submitted:", { 
      position: selectedPosition, 
      question, 
      ...data,
      timestamp: new Date().toISOString()
    });
    
    setIsSubmitted(true);
    form.reset();
    
    // Auto-hide success message after 3 seconds
    setTimeout(() => {
      setIsSubmitted(false);
    }, 3000);
  };

  const resetAll = () => {
    setSelectedPosition("");
    setQuestion("");
    setError("");
    setIsSubmitted(false);
    form.reset();
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-background to-muted/20">
      <main className="container mx-auto px-4 py-8 max-w-4xl">
        {/* Header Section */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold tracking-tight mb-4">
            Interview Question Generator
          </h1>
          <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
            Select a position to generate tailored interview questions and evaluate candidate responses with our comprehensive evaluation form.
          </p>
        </div>

        {/* Position Selection Card */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle className="text-2xl">Step 1: Select Position</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 items-end">
              <div className="space-y-2">
                <label className="text-sm font-medium">Job Position</label>
                <Select onValueChange={(value: string) => setSelectedPosition(value)} value={selectedPosition}>
                  <SelectTrigger className="w-full">
                    <SelectValue placeholder="Choose a position" />
                  </SelectTrigger>
                  <SelectContent>
                    {positions.map((pos) => (
                      <SelectItem key={pos} value={pos}>
                        {pos}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                {error && (
                  <p className="text-sm text-destructive">{error}</p>
                )}
              </div>
              <div className="flex gap-2">
                <Button 
                  onClick={generateQuestion} 
                  className="flex-1"
                  disabled={!selectedPosition}
                >
                  Generate Question
                </Button>
                <Button 
                  onClick={resetAll} 
                  variant="outline"
                  className="flex-1"
                >
                  Reset All
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Generated Question Display */}
        {question && (
          <Card className="mb-8 border-primary/20">
            <CardHeader>
              <CardTitle className="text-2xl flex items-center gap-2">
                Step 2: Interview Question
                <span className="text-sm font-normal text-muted-foreground">
                  for {selectedPosition}
                </span>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="bg-muted/50 rounded-lg p-6">
                <p className="text-lg leading-relaxed">{question}</p>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Evaluation Form */}
        {question && (
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl">Step 3: Candidate Evaluation</CardTitle>
              <p className="text-muted-foreground">
                Rate the candidate's response and provide detailed feedback.
              </p>
            </CardHeader>
            <CardContent>
              {isSubmitted && (
                <div className="mb-6 p-4 bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-lg">
                  <p className="text-green-800 dark:text-green-200 font-medium">
                     Evaluation successfully submitted!
                  </p>
                </div>
              )}
              
              <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <FormField
                      control={form.control}
                      name="candidateName"
                      rules={{ required: "Candidate name is required" }}
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Candidate Name</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="Enter candidate's name"
                              {...field}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    
                    <FormField
                      control={form.control}
                      name="rating"
                      rules={{ 
                        required: "Rating is required",
                        min: { value: 1, message: "Rating must be at least 1" },
                        max: { value: 5, message: "Rating must be at most 5" }
                      }}
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Overall Rating (1-5)</FormLabel>
                          <FormControl>
                            <Input
                              type="number"
                              min="1"
                              max="5"
                              placeholder="Enter rating (1-5)"
                              {...field}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                  
                  <FormField
                    control={form.control}
                    name="feedback"
                    rules={{ required: "Feedback is required" }}
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Detailed Feedback</FormLabel>
                        <FormControl>
                          <Textarea
                            placeholder="Provide detailed feedback on the candidate's response, including strengths, areas for improvement, and overall assessment..."
                            className="min-h-32"
                            {...field}
                          />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  
                  <div className="flex gap-4 pt-4">
                    <Button type="submit" className="flex-1">
                      Submit Evaluation
                    </Button>
                    <Button 
                      type="button" 
                      variant="outline" 
                      onClick={() => form.reset()}
                      className="flex-1"
                    >
                      Clear Form
                    </Button>
                  </div>
                </form>
              </Form>
            </CardContent>
          </Card>
        )}

        {/* Instructions */}
        {!question && (
          <Card className="mt-8 bg-muted/30">
            <CardContent className="pt-6">
              <h3 className="font-semibold mb-3">How to use this tool:</h3>
              <ol className="space-y-2 text-sm text-muted-foreground">
                <li>1. Select a job position from the dropdown menu</li>
                <li>2. Click "Generate Question" to get a tailored interview question</li>
                <li>3. Ask the candidate the generated question during the interview</li>
                <li>4. Fill out the evaluation form based on their response</li>
                <li>5. Submit the evaluation to record your assessment</li>
              </ol>
            </CardContent>
          </Card>
        )}
      </main>
    </div>
  );
}
