export function getRandomQuestion(position: string): string {
  const questions = interviewQuestions[position];
  if (!questions || questions.length === 0) {
    return "No interview question available for the selected position.";
  }
  const randomIndex = Math.floor(Math.random() * questions.length);
  return questions[randomIndex];
}

export function getAvailablePositions(): string[] {
  return Object.keys(interviewQuestions).sort();
}
